import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import Svg from '../Svg';
import Menu from './Menu';
import Form from '../Form';
import Popup from '../Popup';
import "./Navigation.css";

export default class Navigation extends Component {
    constructor(props){
        super(props);
    }

    render(){
        return(
            <header className="navigation navigation--small">
                <div className="navigation__top">
                    <div className="navigation__top-left">
                        <Link to='/' className='logo navigation__logo popup-with-form'><Svg icon="logo" /></Link>
                        <div className="navigation__phone">
                            <span>Телефон:</span>
                            <a href="tel:8(033)333-00-66" className="navigation__phone-tel">8 (033) 333-00-66</a>
                            <Popup
                                className="callback"
                                link={<span className="navigation__phone-callback">обратный звонок</span>}
                            >
                                <Form type="callback" inputs={['name','phone']} popup>
                                    <h3 className="title-accent">Заказать Звонок</h3>
                                </Form>
                            </Popup>
                        </div>
                        <div className="navigation__schedule">
                            <span>График работы</span>
                            <strong>10:00-20:00</strong>
                            <span>ежедневно</span>
                        </div>
                    </div>
                    <div className="navigation__top-right">
                        <Link to='/dostavka' className="toggle-menu__link" >Доставка</Link>
                        <Link to='/rassrochka' className="toggle-menu__link" >Рассрочка</Link>
                    </div>
                </div>
                <div className="navigation__bottom">
                    <Menu {...this.props} />
                </div>
            </header>
        );
    }
}


