import React from 'react';

function DescriptionCollection(props){
    const { characteristics } = props.options;
    return(
        <div className="options-line">
            <h2 className="options-line__head title-accent">Коллекция «{props.options.name_ru}»</h2>
            <table className="advantage-line">
                <tbody>
                    {
                        characteristics.map((characteristic, index) => (
                            <tr key={index}>
                                <td><div className="advantage-line__title">{characteristic.name}:</div></td>
                                <td><div className="advantage-line__text">{characteristic.value}</div></td>
                            </tr>
                        ))
                    }
                </tbody>
            </table>
            <p className="options-line__desc">{props.options.description}</p>
            <div className="assortment-line">
                <h3 className="assortment-line__title">В ассортименте чехлы для:</h3>
                <ul className="assortment-line__wrap">
                    <li>img1</li>
                    <li>img2</li>
                    <li>img3</li>
                    <li>img4</li>
                    <li>img5</li>
                </ul>
            </div>
        </div>
    );
}

export default DescriptionCollection;


